:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.tab_container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  min-width: 0;
  min-height: 0;
  position: relative;
}
.tab_container .tab_bar {
  display: flex;
  align-items: stretch;
  height: 32px;
  overflow-x: auto;
  white-space: nowrap;
  flex-shrink: 0;
}
.tab_container .tab_bar .title {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0 12px;
  margin: 0;
  border-bottom: 1px solid #505050;
}
.tab_container .tab_bar .title .input_title input:hover, .tab_container .tab_bar .title .input_title input:focus {
  margin: -4px;
  padding: 4px;
  background-color: #393939;
}
.tab_container .tab_bar .title.selected {
  border-left: 1px solid #505050;
  border-right: 1px solid #505050;
  margin: 0 -1px;
  border-bottom: none;
  background-color: #242424;
}
.tab_container .tab_bar .title.fake {
  pointer-events: none;
}
.tab_container .tab_bar .title.fake:first-child {
  flex-shrink: 0;
  width: 16px;
}
.tab_container .tab_bar .title.fake:last-child {
  flex: 1;
}
.tab_container .content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: #242424;
  overflow: hidden;
}