:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.graph {
  flex: 1;
  align-self: stretch;
}
.graph .node .circle {
  fill: #505050;
  stroke: #bbbbbb;
  stroke-width: 1;
}
.graph .node .id {
  fill: #bbbbbb;
  alignment-baseline: central;
  text-anchor: middle;
}
.graph .node .weight {
  fill: white;
  font-weight: bold;
  alignment-baseline: central;
  text-anchor: left;
}
.graph .node.selected .circle {
  fill: #2962ff;
  stroke: #2962ff;
}
.graph .node.visited .circle {
  fill: #c51162;
  stroke: #c51162;
}
.graph .edge .line {
  stroke: #bbbbbb;
  stroke-width: 2;
}
.graph .edge .line.directed {
  marker-end: url(#markerArrow);
}
.graph .edge .weight {
  fill: #bbbbbb;
  alignment-baseline: baseline;
  text-anchor: middle;
}
.graph .edge.selected .line {
  stroke: #2962ff;
}
.graph .edge.selected .line.directed {
  marker-end: url(#markerArrowSelected);
}
.graph .edge.selected .weight {
  fill: #2962ff;
}
.graph .edge.visited .line {
  stroke: #c51162;
}
.graph .edge.visited .line.directed {
  marker-end: url(#markerArrowVisited);
}
.graph .edge.visited .weight {
  fill: #c51162;
}
.graph .arrow {
  fill: #bbbbbb;
}
.graph .arrow.selected {
  fill: #2962ff;
}
.graph .arrow.visited {
  fill: #c51162;
}