:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.toast_container {
  display: flex;
  flex-direction: column-reverse;
  padding: 16px;
  pointer-events: none;
}

.toast {
  width: 280px;
  border: 1px solid;
  border-radius: 4px;
  padding: 16px;
  margin: 8px;
  white-space: pre-wrap;
  pointer-events: auto;
  font-family: "Monaco", "Menlo", "Ubuntu Mono", "Consolas", "source-code-pro", monospace;
}
.toast.success {
  border-color: rgb(0, 150, 0);
  background-color: rgba(0, 120, 0, 0.8);
}
.toast.error {
  border-color: rgb(150, 0, 0);
  background-color: rgba(120, 0, 0, 0.8);
}