:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.button {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0 12px;
  margin: 0;
}
.button .icon {
  margin-right: 4px;
}
.button .icon.image {
  width: 1.6em;
  height: 1.6em;
  background-position: center;
  background-size: cover;
  border-radius: 2px;
}
.button.reverse {
  flex-direction: row-reverse;
}
.button.reverse .icon {
  margin-right: 0;
  margin-left: 4px;
}
.button.icon_only .icon {
  margin-left: 0;
  margin-right: 0;
}
.button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.button.primary:hover {
  background-color: rgba(0, 0, 0, 0.2);
}
.button.primary:hover:active {
  box-shadow: 0px 0px 10px 3px #1a1a1a inset;
}
.button.primary.active {
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 10px 3px #1a1a1a inset;
  font-weight: bold;
}
.button.primary.active .icon {
  color: #00e676;
}
.button.selected {
  background-color: rgba(0, 0, 0, 0.2);
}
.button.selected:hover {
  color: rgba(187, 187, 187, 0.8);
}
.button.disabled {
  cursor: not-allowed;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 0.6;
}
.button.confirming {
  color: #f3bd58;
}