:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.navigator {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
}
.navigator .search_bar_container {
  height: 32px;
  padding: 0 8px;
  display: flex;
  align-items: stretch;
  border-bottom: 1px solid #505050;
}
.navigator .search_bar_container:focus-within {
  background-color: rgba(255, 255, 255, 0.1);
}
.navigator .search_bar_container .search_icon {
  align-self: center;
  margin-right: 8px;
}
.navigator .search_bar_container .search_bar {
  flex: 1;
  box-sizing: border-box;
}
.navigator .algorithm_list {
  flex: 1;
  overflow-y: auto;
}
.navigator .footer {
  max-height: 30vh;
  border-top: 1px solid #505050;
  overflow-y: auto;
}