:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.progress_bar {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: #505050;
  cursor: pointer;
  pointer-events: auto;
}
.progress_bar > * {
  pointer-events: none;
}
.progress_bar .active {
  position: absolute;
  height: 100%;
  left: 0;
  background-color: #00e676;
}
.progress_bar .label {
  position: absolute;
  color: #242424;
}
.progress_bar .label .current {
  font-weight: bold;
}