:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.log {
  flex: 1;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  overflow-y: auto;
}
.log .content {
  padding: 24px;
  font-family: "Monaco", "Menlo", "Ubuntu Mono", "Consolas", "source-code-pro", monospace;
  white-space: pre-wrap;
  line-height: 1.6;
}