:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.code_editor {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.code_editor .ace_editor {
  flex: 1;
  width: 100% !important;
  height: 100% !important;
  min-width: 0 !important;
  min-height: 0 !important;
}
.code_editor .ace_editor .current_line_marker {
  background-color: rgba(34, 153, 221, 0.4);
  border: 1px solid #29d;
  position: absolute;
  width: 100% !important;
  animation: line_highlight 0.1s;
}
@keyframes line_highlight {
  from {
    background-color: rgba(34, 153, 221, 0.1);
  }
  to {
    background-color: rgba(34, 153, 221, 0.4);
  }
}
.code_editor .contributors_viewer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 4px;
  background-color: #393939;
}
.code_editor .contributors_viewer .contributor {
  height: 28px;
  padding: 0 6px;
  font-weight: bold;
}
.code_editor .contributors_viewer .contributor.label {
  display: flex;
  align-items: center;
  white-space: nowrap;
}
.code_editor .contributors_viewer .empty {
  display: flex;
  flex: 1;
}
.code_editor .contributors_viewer .delete {
  height: 32px;
}