:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.array_2d {
  flex-shrink: 0;
  display: table;
  border-collapse: collapse;
}
.array_2d .row {
  display: table-row;
  height: 28px;
}
.array_2d .row .col {
  display: table-cell;
  text-align: center;
  min-width: 28px;
  background-color: #393939;
  border: 1px solid #505050;
  padding: 0 4px;
}
.array_2d .row .col .value {
  font-size: 12px;
}
.array_2d .row .col.selected {
  background-color: #2962ff;
}
.array_2d .row .col.patched {
  background-color: #c51162;
}
.array_2d .row .col.index {
  background: none;
  border: none;
  color: #505050;
}