:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

html,
body,
#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  user-select: none;
  color: #bbbbbb;
  font-size: 12px;
}

a {
  text-decoration: none;
  color: inherit;
}

* {
  box-sizing: border-box;
}

input,
select,
textarea,
button {
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  background: none;
  border: none;
  outline: none;
  min-width: 0;
  margin: 0;
  padding: 0;
  line-height: 1.15;
}