:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.header {
  display: flex;
  flex-direction: column;
  min-width: 0;
}
.header .row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 16px;
  border-bottom: 1px solid #505050;
}
.header .row .section {
  height: 32px;
  display: flex;
  align-items: stretch;
}
.header .row .section .title_bar {
  font-size: 14px;
  font-weight: bold;
  min-width: 0;
}
.header .row .section .title_bar .nav_arrow {
  margin: 0 4px;
}
.header .row .section .title_bar .nav_caret {
  margin-left: 4px;
}
.header .row .section .title_bar .input_title {
  padding: 4px 8px;
  background-color: #505050;
}
.header .row .section .btn_dropdown {
  position: relative;
  font-weight: bold;
}
.header .row .section .btn_dropdown:active {
  box-shadow: none;
}
.header .row .section .btn_dropdown .dropdown {
  z-index: 98;
  position: absolute;
  left: 0;
  top: 0;
  display: none;
  flex-direction: column;
  align-items: stretch;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  background-color: #505050;
  margin-top: 32px;
}
.header .row .section .btn_dropdown:hover .dropdown {
  display: flex;
}