:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.divider {
  position: relative;
  z-index: 97;
}
.divider:after {
  position: absolute;
  background-color: #505050;
  content: "";
}
.divider.horizontal {
  width: 7px;
  margin: 0 -3px;
  cursor: ew-resize;
}
.divider.horizontal:after {
  top: 0;
  bottom: 0;
  left: 3px;
  width: 1px;
}
.divider.vertical {
  height: 7px;
  margin: -3px 0;
  cursor: ns-resize;
}
.divider.vertical:after {
  left: 0;
  right: 0;
  top: 3px;
  height: 1px;
}