:export {
  themeDark: #242424;
  themeNormal: #393939;
  themeLight: #505050;
  colorFont: #bbbbbb;
  colorShadow: rgba(0, 0, 0, 0.2);
  colorOverlay: rgba(255, 255, 255, 0.1);
  colorAlert: #f3bd58;
  colorSelected: #2962ff;
  colorPatched: #c51162;
  colorHighlight: #29d;
  colorActive: #00e676;
}

:export {
  lineHeight: 32px;
  fontSizeNormal: 12px;
  fontSizeLarge: 14px;
}

.player .progress_bar {
  width: 160px;
}
.player .speed {
  display: flex;
  align-items: center;
  padding: 0 12px;
  white-space: nowrap;
}
.player .speed:hover {
  background-color: rgba(0, 0, 0, 0.2);
}
.player .speed .range {
  position: relative;
  height: 16px;
  width: 60px;
  margin-left: 8px;
}
.player .speed .range .range_label_container {
  display: none;
}
.player .speed .range .range_track {
  top: 50%;
  height: 6px;
  margin-top: -3px;
  background-color: #505050;
  cursor: pointer;
  display: block;
  position: relative;
}
.player .speed .range .range_slider {
  top: 0;
  width: 6px;
  height: 12px;
  margin-left: -3px;
  margin-top: -3px;
  appearance: none;
  background-color: #bbbbbb;
  cursor: pointer;
  display: block;
  position: absolute;
}